// Variables
// ---------
:root {
	--body-font-size                            : 16px;
	--body-font                                 : "Inter", system-ui, -apple-system, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
	--body-font-alt                             : Georgia, Times, Times New Roman, serif;
	--body-line-height                          : 161%;
	--body-color                                : #FFF;
	--body-background                           : #74A9B5;

	// Table
	--table-header-font-weight                  : 700;
	--table-cell-padding                        : 5px 10px;

	// Text
	--heading-font-weight                       : 700;
	--heading-line-height                       : 1.2;
	--heading-margin                            : 0 0 1em;
	--paragraph-margin                          : 0 0 1em;
	--list-margin                               : 1em 0;
	--h1                                        : 28px;
	--h2                                        : 24px;
	--h3                                        : 20px;
	--h4                                        : 18px;
	--h5                                        : 16px;
	--bold-font-weight                          : 700;
	--text-small                                : 14px;

	// Color

	--red                                       : #F61001;
	--link-color                                : #203735;
	--link-color-hover                          : #37605C;
	--green-color                               : linear-gradient(180deg, #00FF1E 0%, #048211 100%);
	--white-color                               : #FFF;
	--gold-color                                : #FFC600;

	// Swiper
	--swiper-theme-color                        : var(--main);
	--swiper-preloader-color                    : var(--swiper-theme-color);
	--swiper-wrapper-transition-timing-function : initial;
}

// Breakpoints
$break-desktop     : 1081px;
$break-desktop-max : (1081 - 0.02) * 1px;
$break-table       : 681px;
$break-table-max   : (681 - 0.02) * 1px;
