.contacts {
	margin-bottom : 72px;
}

.contacts__wrapper {
	padding : 10px 12px;

	@media screen and (min-width : $break-desktop) {
		padding : 16px 93px;
	}
}

.contacts__text {
	margin-bottom : 10px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 20px;
	}
}

.contacts__list {
	padding    : 0;
	margin     : 0;
	list-style : none;
}

.contact__item {
	font-weight : 400;
	font-size   : 14px;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}

	&:not(:last-child) {
		margin-bottom : 7px;
		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}
	}
}

.contact__name {
	color : var(--gold-color);
}

.contact__link {
	color : var(--white-color);

	a {
		color           : var(--white-color);
		text-decoration : none;

		&:hover {
			color : var(--white-color);
		}
	}

}
