.play {
	margin-bottom : 69px;
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 124px;
	}
}

.play__wrapper {
	padding : 14px 14px;
}

.play__item {
	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.play__title {
	color : var(--gold-color);
}

.play__text {
	&:not(:last-child) {
		margin-bottom : 20px;
	}
}
