.about {
	margin-bottom : 20px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 68px;
	}
}

.about__main {
	padding : 10px 11px;

	@media screen and (min-width : $break-desktop) {
		padding : 28px 29px;
	}
}

.about__top {
	margin-bottom : 27px;

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		align-items     : flex-start;
		justify-content : space-between;
	}
}

.about__left {
	margin-bottom : 12px;
	@media screen and (min-width : $break-desktop) {
		max-width     : 563px;
		margin-bottom : 0;
	}
}

.about__text {
	font-weight : 400;
	font-size   : 16px;
	color       : var(--white-color);

	&:not(:last-child) {
		margin-bottom : 20px;
	}
}

.about__list {
	list-style : none;
	padding    : 0;
	margin     : 0;

	li {
		div {
			font-weight    : 700;
			font-size      : 14px;
			text-transform : uppercase;
			color          : var(--gold-color);
			margin-bottom  : 6px;

			@media screen and (min-width : $break-desktop) {
				font-weight : 700;
			}
		}

		span {
			font-weight : 400;
			font-size   : 14px;
			color       : var(--white-color);

			@media screen and (min-width : $break-desktop) {
				font-size : 16px;
			}
		}

		&:not(:last-child) {
			margin-bottom : 5px;
			@media screen and (min-width : $break-desktop) {
				margin-bottom : 14px;
			}
		}
	}
}

.about__items {
	display        : flex;
	flex-direction : column;
	align-items    : center;

	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
		flex-direction  : row;
	}
}


.about-item {
	display        : flex;
	flex-direction : column;
	align-items    : center;

	&__img {
		margin-bottom : 27px;
	}

	.b-btn--default {
		padding   : 9px 16px;
		min-width : 259px;
		@media screen and (min-width : $break-desktop) {
			min-width : 178px;
		}

		span {
			display     : block;
			white-space : normal;
			@media screen and (min-width : $break-desktop) {
				max-width : 135px;
			}
		}
	}
}
