.bonus__wrapper {
	background-image      : url("../images/bonus/bg-mob.png");
	background-repeat     : no-repeat;
	background-size       : cover;
	box-shadow            : 0 0 4px 0 rgba(0, 0, 0, 0.25);
	border                : 1px solid var(--gold-color);
	border-radius         : 20px;
	padding               : 13px 13px 0 13px;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	align-items           : center;
	margin-bottom         : 20px;

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
		background-image      : url("../images/bonus/bg-desktop.png");
		margin-bottom         : 68px;
	}

}

.bonus__logo {
	grid-column   : 4 span;
	border-radius : 20px;
	background    : #1A4E38;
	text-align    : center;
	border        : 1px solid #FFF;
	min-width     : 259px;
	margin        : 0 auto 24px auto;
	@media screen and (min-width : $break-desktop) {
		grid-column   : 3 span;
		margin-top    : -13px;
		min-width     : 208px;
		margin-bottom : 0;
		margin-left   : 0;
	}
}

.bonus__wrap {
	grid-column           : 4 span;
	display               : grid;
	grid-template-columns : subgrid;
	align-items           : center;
	max-width             : 300px;
	@media screen and (min-width : $break-desktop) {
		grid-column  : 9 span;
		grid-row-gap : 10px;
	}

	.b-btn--default {
		grid-column : 4 span;
		min-width   : 259px;
		margin      : 28px auto 16px auto;
		padding     : 13px 13px;
		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
			margin      : 0 auto;
			padding     : 19px 13px;
			min-width   : 184px;
		}
	}
}

.bonus-item {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	height         : 100%;


	&:nth-child(1) {
		grid-column : 2 span;

		@media screen and (min-width : $break-desktop) {
			grid-column : 2/5;
		}
	}

	&:nth-child(2) {
		grid-column : 2 span;

		@media screen and (min-width : $break-desktop) {
			grid-column : 5/7;
		}
	}
}

.bonus-item__name {
	font-weight   : 400;
	font-size     : 16px;
	text-align    : center;
	color         : #00FF1E;
	margin-bottom : 20px;
	display       : none;
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.bonus-item__text {
	text-align  : center;
	font-weight : 700;
	font-size   : 16px;
	color       : var(--white-color);

}

.bonus-item__about {
	text-align  : center;
	font-weight : 700;
	color       : var(--gold-color);
	font-size   : 15px;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.bonus-item__list {
	list-style : none;
	display    : flex;
	padding    : 0;
	margin     : auto 0 0 0;

}

.bonus__text {
	font-weight : 400;
	font-size   : 10px;
	color       : #FFF;
	grid-column : 4 span;
	text-align  : center;
	@media screen and (min-width : $break-desktop) {
		grid-column : 1/10;
		font-size   : 12px;
	}
}

.casino-text__wrapper {
	padding       : 19px 12px;
	margin-bottom : 20px;
	@media screen and (min-width : $break-desktop) {
		padding       : 16px 14px;
		margin-bottom : 68px;
	}

	div {
		font-weight : 400;
		font-size   : 16px;
		color       : var(--white-color);

		&:not(:last-child) {
			margin-bottom : 15px;
		}
	}
}

.box-default {
	background    : #1A4E38;
	border        : 1px solid #C19600;
	border-radius : 20px;
	overflow      : hidden;
}

.section-title {
	font-weight    : 700;
	font-size      : 14px;
	text-transform : uppercase;
	text-align     : center;
	color          : var(--gold-color);

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}

.text {
	font-weight : 400;
	font-size   : 14px;
	color       : var(--white-color);

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}
