.hero {
	background-image      : url("../images/hero/hero-bg.jpg");
	background-repeat     : no-repeat;
	background-position-x : center;
	padding-bottom        : 20px;
	margin-bottom         : 20px;
	@media screen and (min-width : $break-desktop) {
		padding         : 16px 0 45px 0;
		background-size : cover;
		margin-bottom   : 68px;
	}

}

.hero__wrapper {
	display        : flex;
	flex-direction : column;
	align-items    : center;
}

.hero__img {
	margin-bottom : 13px;
	width         : 64px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 21px;
		width         : 138px;
	}
}

.hero__title {
	font-weight   : 700;
	font-size     : 32px;
	text-align    : center;
	color         : var(--white-color);
	margin-bottom : 13px;

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		margin-bottom : 24px;
	}
}

.hero__list {
	display               : grid;
	grid-template-columns : repeat(2, auto);
	list-style            : none;
	margin                : 0 0 13px 0;
	padding               : 0;
	grid-gap              : 7px 20px;

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		align-items     : center;
		flex-wrap       : wrap;
		justify-content : center;
		gap             : 34px;
		margin          : 0 0 22px 0;
	}
}

.hero__item {
	font-weight : 700;
	font-size   : 14px;
	color       : var(--gold-color);
	grid-column : 1 span;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
		width     : auto;
	}

	&:before {
		content          : "";
		display          : inline-block;
		background-image : url("../images/icons/check.svg");
		background-size  : cover;
		width            : 18px;
		height           : 13px;
	}
}

.hero__text {
	font-weight : 400;
	font-size   : 14px;
	color       : var(--white-color);
	text-align  : center;
	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}
}
