// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 1.25;
$btn-border-width          : 30px;
$btn-padding               : 19px 44px;
$btn-font-size             : 16px;
$btn-border-radius         : 30px;
$btn-transition            : background .3s ease, color .3s ease, border-color .3s ease;
$btn-height                : 49px;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 4px 30px;
$btn-font-size-big         : 18px;
$btn-height-big            : 59px;

// Small
$btn-padding-small         : 4px 16px;
$btn-font-size-small       : 15px;
$btn-height-small          : 39px;

// COLOR
// White
$btn-white-bg              : #DCDCDE;
$btn-white-color           : #0B081B;
$btn-white-bg-hover        : #BDBDBF;
$btn-white-color-hover     : #0B081B;

// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	font-size       : $btn-font-size;
	font-weight     : $btn-font-weight;
	line-height     : $btn-line-height;
	position        : relative;
	display         : inline-flex;
	align-items     : center;
	flex-flow       : row nowrap;
	justify-content : center;
	box-sizing      : border-box;
	min-height      : $btn-height;
	padding         : $btn-padding;
	user-select     : none;
	transition      : $btn-transition;
	text-align      : center;
	vertical-align  : middle;
	white-space     : nowrap;
	text-decoration : none;
	border-radius   : $btn-border-radius;
	text-transform  : uppercase;
	border          : 1px solid var(--white-color);

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		cursor     : default;
		opacity    : .45;
		background : inherit;
		box-shadow : none;
	}

	&:active,
	&.active {

	}

	> svg {
		display : block;
		flex    : 0 0 auto;

		&:first-child {
			margin-right : 6px;
		}

		&:last-child {
			margin-left : 6px;
		}
	}

	// SIZE
	// Big
	&--big {
		font-size  : $btn-font-size-big;
		min-height : $btn-height-big;
		padding    : $btn-padding-big;
	}

	&--small {
		font-size  : $btn-font-size-small;
		min-height : $btn-height-small;
		padding    : $btn-padding-small;
	}

	// COLOR
	// Red
	&--default {
		background : linear-gradient(180deg, #00FF1E 0%, #048211 100%);
		color      : var(--white-color);
		position   : relative;
		overflow   : hidden;

		&:before {

			content        : '';
			position       : absolute;
			top            : 0;
			left           : -75%;
			width          : 50%;
			height         : 100%;
			background     : rgba(255, 255, 255, 0.2);
			transform      : skewX(-20deg);
			transition     : none;
			animation      : shine 5s infinite;
			pointer-events : none;

		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			color       : var(--white-color);
			animation   : shake 0.82s cubic-bezier(.36, .07, .19, .97) both;
			transform   : translate3d(0, 0, 0);
			perspective : 1000px;
			filter      : brightness(1.1) hue-rotate(-70deg) drop-shadow(0 -.125em .125em #FFDE00);
			box-shadow  : 0px 8px 24px -3px rgba(255, 255, 255, 1);
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {

			}
		}
	}
}

@keyframes shake {
	10%, 90% {
		transform : translate3d(-1px, 0, 0);
	}
	20%, 80% {
		transform : translate3d(2px, 0, 0);
	}
	30%, 50%, 70% {
		transform : translate3d(-4px, 0, 0);
	}
	40%, 60% {
		transform : translate3d(4px, 0, 0);
	}
}

@keyframes shine {
	0% {
		left : -75%;
	}
	50% {
		left : 100%;
	}
	100% {
		left : 100%;
	}
}
