.b-btn {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  position: relative;
  display: inline-flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
  box-sizing: border-box;
  min-height: 49px;
  padding: 19px 44px;
  user-select: none;
  transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border-radius: 30px;
  text-transform: uppercase;
  border: 1px solid var(--white-color);
}
.b-btn:hover, .b-btn:focus-within, .b-btn.focus {
  outline: 0;
}
.b-btn.disabled, .b-btn:disabled {
  cursor: default;
  opacity: 0.45;
  background: inherit;
  box-shadow: none;
}
.b-btn > svg {
  display: block;
  flex: 0 0 auto;
}
.b-btn > svg:first-child {
  margin-right: 6px;
}
.b-btn > svg:last-child {
  margin-left: 6px;
}
.b-btn--big {
  font-size: 18px;
  min-height: 59px;
  padding: 4px 30px;
}
.b-btn--small {
  font-size: 15px;
  min-height: 39px;
  padding: 4px 16px;
}
.b-btn--default {
  background: linear-gradient(180deg, #00FF1E 0%, #048211 100%);
  color: var(--white-color);
  position: relative;
  overflow: hidden;
}
.b-btn--default:before {
  content: "";
  position: absolute;
  top: 0;
  left: -75%;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.2);
  transform: skewX(-20deg);
  transition: none;
  animation: shine 5s infinite;
  pointer-events: none;
}
.b-btn--default:hover, .b-btn--default:focus-within, .b-btn--default:active, .b-btn--default.active {
  color: var(--white-color);
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;
  filter: brightness(1.1) hue-rotate(-70deg) drop-shadow(0 -0.125em 0.125em #FFDE00);
  box-shadow: 0px 8px 24px -3px rgb(255, 255, 255);
}
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }
  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
@keyframes shine {
  0% {
    left: -75%;
  }
  50% {
    left: 100%;
  }
  100% {
    left: 100%;
  }
}
