.footer {
	background : #515151;
	padding    : 19px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 47px 0 34px 0;
	}
}

.footer__text {
	text-align  : center;
	font-family : var(--font-family);
	font-weight : 400;
	font-size   : 14px;
	color       : var(--white-color);
	margin      : 0 auto 20px auto;

	@media screen and (min-width : $break-desktop) {
		max-width : 810px;
		margin    : 0 auto 30px auto;
		font-size : 16px;
	}
}


.footer__partners {
	text-align      : center;
	justify-content : center;
	display         : flex;
	gap             : 20px;
	margin-bottom   : 20px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 30px;
		gap           : 27px;
	}
}

.nav {
	display         : flex;
	justify-content : center;
	margin-bottom   : 20px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 30px;
	}
}

.nav__list {
	list-style     : none;
	display        : flex;
	flex-direction : column;
	gap            : 20px;
	align-items    : center;
	padding        : 0;
	margin         : 0;
	@media screen and (min-width : $break-desktop) {
		flex-direction : row;
		gap            : 37px;
	}
}

.nav__link {
	font-weight     : 400;
	font-size       : 14px;
	color           : var(--white-color);
	text-decoration : none;

	@media screen and (min-width : $break-desktop) {
		font-size : 16px;
	}

	&:hover {
		color : var(--white-color);
	}
}

.footer__icon {
	text-align : center;
}
